.container {
    position: absolute;
    width: 100%;
    padding: 40px;
}

.sub {
    width: 80%;
    margin: 50px auto;
    max-width: 100%;
    display: grid;
    grid-template-columns: 50% 50%;
    min-height: 450px;
    overflow: hidden;
}

.box1 {
    position: relative;
    background-color: #fff;
    /**box-shadow: 5px 5px 5px #bdbdbd, 0 0 10px #bdbdbd;**/
    width: 100%;
    border-radius: 15px;
    height: 420px;
    overflow: hidden;
    text-align: center;
}

.box1 img{
    width: 100%;
    height: 420px;
}

.box2 {
    padding: 10%;
    line-height: 2;
}

.box2 a{
    text-decoration: underline;
    font-weight: bold;
}

.box2 p{
    font-size: 1.2em;
}

@media all and (max-width: 900px){
    .sub {
        margin-top: 10vh;
        grid-template-columns: repeat(auto-fill, minmax(100% , 1fr));
        width: 100%;
    }
    .box1 {
        width: 100%;
    }
    .box1 h2{
        padding-top: 15px;
    }
}