
.home {
    position: absolute;
    width: 100%;
    height: 90vh;
}

/** logo **/
.logo {
    position: absolute;
    top: 50%;
    left: 50%;
    height: 20vh;
    transform: translate(-50%, -50%);
    animation: fill 1s ease forwards 3s;
    z-index: -1;
}

.logo path:nth-child(1) {
    stroke-dasharray: 625.7;
    stroke-dashoffset: 625.7;
    animation: line-anim 2s ease forwards;
}

.logo path:nth-child(2) {
    stroke-dasharray: 729.398;
    stroke-dashoffset: 729.398;
    animation: line-anim 2s ease forwards 0.3s;
}

.logo path:nth-child(3) {
    stroke-dasharray: 867.5;
    stroke-dashoffset: 867.5;
    animation: line-anim 2s ease forwards 0.6s;
}

.logo path:nth-child(4) {
    stroke-dasharray: 845;
    stroke-dashoffset: 845;
    animation: line-anim 2s ease forwards 0.9s;
}

.logo path:nth-child(5) {
    stroke-dasharray: 845;
    stroke-dashoffset: 845;
    animation: line-anim 2s ease forwards 1.2s;
}

.quote {
    position: absolute;
    text-align: center;
    width: 96%;
    bottom: 12%;
    margin: 0 2%;
}

.quote p , .quote h2{
    margin-bottom: 1rem;
    opacity: 0;
    animation: opat 2s ease forwards 2.5s;
}

.quote p {
    font-size: 1.2rem;
}

@keyframes line-anim {
    to {
        stroke-dashoffset: 0;
    }
}
@keyframes fill {
    from {
        fill: transparent;
    }
    to {
        fill: black;
    }
}

@keyframes opat {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@media all and (max-height: 400px){
    .quote {
        display: none;
    }
}