.container {
    position: absolute;
    width: 100%;
    max-width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(50% , 1fr));
    /**grid-template-rows: minmax(100px, auto);**/
    padding: 40px;
    grid-auto-flow: dense;
    /**grid-gap: 20px;**/
    height: 90vh;
}

.container .box {
    padding: 20px;
    display: grid;
    font-size: 20px;
    place-items: center;
    text-align: center;
    height: 80vh;
}

.container .box:nth-child(1) {
    min-height: 400px;
    height: 40vh;
    transform: translateY(165px);
    background-color: #212121;
    border-radius: 15px;
    box-shadow: 5px 5px 5px #bdbdbd, 0 0 40px #bdbdbd;
    margin-bottom: 200px;
}

.container .box:nth-child(1) .content {
    transform: translateY(-165px);
}

.container .box:nth-child(1) * {
    color: #fff;
    margin-bottom: 15px;
}

.container .box p {
    padding: 10px 10%;
    text-align: justify;
    line-height: 1.4;
}

.container .box .intro {
    text-align: center;
}

.container .box img {
    position: relative;
    height: 30vh;
    width: 30vh;
    /*height: 30vh;*/
    margin-bottom: 10px;
    border-radius: 100%;
	-webkit-border-radius: 100%;
    -moz-border-radius: 100%;
    box-shadow: 0 -10px 20px rgba(184, 184, 184, 0.5);
}

.progress {
    width: 200px;
    height: 10px;
    background-color:#212121;
    border-radius: 5px;
    margin-left: 10%;
}

.high {
    width: 160px;
    height: 10px;
    background-color: #4bbf73;
    border-radius: 5px;
}

.medium {
    width: 120px;
    height: 10px;
    background-color: #4bbf73;
    border-radius: 5px;
}

.low {
    width: 80px;
    height: 10px;
    background-color: #4bbf73;
    border-radius: 5px;
}

@media all and (max-width: 900px){
    .container {
        margin-top: 10vh;
        grid-template-columns: repeat(auto-fill, minmax(100% , 1fr));
    }
    .container .box img {
        height: 250px;
    }
}