.container {
    position: absolute;
    width: 100%;
    max-width: 100%;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    padding: 0 15%;
    grid-gap: 2%;
    overflow: hidden;
    height: 65vh;
    /** border: 1px solid black;**/
}

.container .box {
    position: relative;
    display: grid;
    font-size: 20px;
    place-items: center;
    text-align: center;
    height: 30vh;
    margin-top: 1vh;
    min-height: 200px;
    transition: 0.5s;
    overflow: hidden;
    border-radius: 15px;
    box-shadow: 5px 5px 5px #bdbdbd, 0 0 10px rgba(189, 189, 189, 0.5);
}

.container .box:nth-child(2) {
    grid-row: span 2;
    grid-column: span 2;
    height: 63.3vh;
}

.container .box:nth-child(2) .content h2 {
    margin-top: 23vh;
}

.container .box .imgBx {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transition: 0.5s;
    padding: 50px;
}

.container .box .imgBx img {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
}

.container .box .content {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    max-width: 100%;
    height: 100%;
    padding: 20px;
    opacity: 0;
    transition: 0.5s;
    /* 212121 */
    background-color: rgba(33, 33, 33, 0.9);
}

.container .box .content h2 {
    margin-top: 7vh;
    margin-bottom: 13px;
    color: #fff;
}

.container .box .content p {
    color: #fff;
    text-align: center;
}

.container .box:hover .content {
    opacity: 1;
}

.container .box .content a{
    display: block;
    width: 100px;
    margin: 20px auto 0;
    padding: 0.5rem;
    background-color: red;
    border: 2px solid red;
    border-radius: 5px;
    color: #fff;
    font-size: 1rem;
    font-weight: 600;
    transition: 0.5s;
}

.container .box .content a:hover {
    color: red;
    background-color: white;
    cursor: pointer;
}

@media all and (max-width: 900px){
    .container {
        margin-top: 10vh;
    }
}

@media all and (max-width: 1200px){
    .container {
        grid-template-columns: repeat(auto-fill, minmax(45% , 1fr));
        height: 100vh;
    }
    .container .box:nth-child(2){
        grid-row: span 1;
        grid-column: span 1;
        height: 30vh;
    }
    .container .box .content h2 {
        margin-top: 4vh;
    }
    .container .box:nth-child(2) .content h2 {
        margin-top: 4vh;
    }
    .container .box:nth-child(3) {
        grid-column: span 2;
    }
}

@media all and (max-width: 700px){
    .container .box:nth-child(3),  .container .box:nth-child(2){
        grid-column: span 1;
    }
    .container {
        grid-template: repeat(6, 15%) / 100%;
        height: 200vh;
        min-height: 1500px;
    }
}

@media all and (max-height: 630px){
    .container {
        margin-top: 50px;
        height: 700px;
    }
}